<template>
  <div class="category-items text-center" v-if="categories.length">
    <router-link v-for="(item, index) in categories" :key="index" :to="`/category/${item.id}`" class="d-block card category-items__item text-decoration-none mb-2">
      <div class="card-body">
        <p class="card-text m-0">{{ item.doc.title }}</p>
      </div>
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'Category',
  data() {
    return {
      categories: [],
    }
  },
  methods: {
    async getCategories() {
      try {
        await this.$pouch.allDocs({
          include_docs: true,
          attachments: true
        }, 'category').then((res) => {
          this.categories = res.rows
        });
      } catch (error) {
        console.error(error)
      }
    }
  },
  mounted() {
    this.getCategories();
  }
}
</script>

<style scoped lang="scss">
.category-items {
  &__item {
    color: #0f5132;
    background-color: #ecf5f1;
    .card-body {
      padding: 8px 10px;
      p {
        font-size: 18px;
      }
    }
  }
}
</style>
